			z-index: 104;
			top: 0;
			padding-top: 0;
			margin-right: 0;
			margin-bottom: 0;
			background-color: $c_gr_001;
			box-shadow: 0 0 20px $c_b_001;
			h1 {
				margin-bottom: 0;
				padding-bottom: 0; }
			img {
				width: auto;
				height: (40/$body_size)+rem;
				margin: 12px 8px;

 } } }				// margin-top: 3px
	.navbar-nav {
		margin-left: 0 !important;
		text-align: center;
		.nav-item {
			padding-left: 1rem;
			padding-right: 1rem;
			.nav-link {
				color: $c_w_001;
				font-size: 20px;
				padding-top: 0.5em;
				padding-bottom: 0.5em;
				text-align: center;
				// letter-spacing: 1em
				border-bottom: 1px solid $c_ye_001;
				&.open {
					color: $c_gr_001;
					&:after {
						transform: rotate(180deg); } } }
			&.dropdown {
				> .dropdown-toggle::after {
					position: absolute;
					top: 30px; } }
			+ .nav-item {
				.nav-link {
 } }					// border-top: 1px solid $c_g_002
			.lang_wrap {
				display: flex;
				justify-content: center;
				align-items: center;
				margin-top: 15px;
				margin-bottom: 15px;
				.lang {
					margin: 0 7px;
					a {
						padding: 2px 9px;
						border: 1px solid $c_ye_002;
						border-radius: 20px;

						color: $c_ye_001;
						font-size: 20px;
						font-weight: 400;
						text-decoration: none; }
					&.active {
						a {
							background: $c_ye_002;
							color: $c_gr_001; } } } } }
		.nav-link {
			padding-left: 1rem;
			padding-right: 1rem;

			padding-left: 1rem;
			padding-right: 0rem; } }


	.navbar-collapse {
		.navbar-nav {
			position: fixed;
			z-index: 101;
			width: 100%;
			height: auto;
			top: 0;
			left: 0;
			right: 0;
			background-color: $c_gr_001;
			padding-top: 10px;
			padding-bottom: 10vh;
			transform: translateY(-1000px);
			will-change: transform;
			overflow: auto; }
		&.show {
			.navbar-nav {
				transform: translateY((60/$body_size)+rem);
				transition: transform .6s cubic-bezier(0.190, 1.000, 0.220, 1.000); } } } }
