    width: 100%;
  }
  .wrap .sec#service .contactArea .btn_sent {
    width: auto;
    margin: 15px 0;
  }
  .wrap .sec#service .contactArea .btn_sent button {
    width: 100%;
  }
  .wrap footer {
    padding-bottom: 50px;
  }
}

@media screen and (max-width: 575px) {
  .wrap .sec#recruit .person_m {
