@keyframes rotate_360 {
	0% {
		transform: rotate(0deg); }

	50% {
		transform: rotate(-180deg); }

	100% {
		transform: rotate(-360deg); } }

@keyframes oclock {
	0% {
		transform: rotate(0deg); }

	100% {
		transform: rotate(+6deg); } }

@keyframes move_bg {
	$i: 0;

	@while $i < 101 {
		$i: $i + 5;

		#{$i}% {
			background-position: ($i * 100px) 0; } } }

@mixin imgGlitch($name, $intensity, $width, $height, $top, $left) {
	$steps: $intensity;

	// Ensure the @keyframes are generated at the root level
	@at-root {
		// We need two different ones
		@for $i from 1 through 2 {
			@keyframes #{$name}-anim-#{$i} {
				@for $i from 0 through $steps {
					#{percentage($i * 1 / $steps)} {
						clip: rect(random($height) + px, $width + px, random($height) + px, 0); } } } } }

	> img {
		position: absolute;
		top: $top + px;
		left: $left + px; }

	> img:nth-child(2),
	> img:nth-child(3) {
		clip: rect(0, 0, 0, 0); }

	> img:nth-child(1) {
		animation: #{$name}-anim-1 7s infinite linear alternate-reverse; }

	> img:nth-child(2) {
		left: $left + 2 + px;
		animation: #{$name}-anim-2 1s infinite linear alternate-reverse; }

	> img:nth-child(3) {
		left: $left - 2 + px;
		animation: #{$name}-anim-3 2s infinite linear alternate-reverse; } }

/*star*/
@keyframes move_UpDown {
	0% {
		transform: translateY(8px); }

	100% {
		transform: translateY(18px); } }
@keyframes move_right {
	0% {
		transform: rotate(0deg) translateX(8px) rotate(0deg); }

	100% {
		transform: rotate(360deg) translateX(8px) rotate(-360deg); } }

@keyframes move_left {
	0% {
		transform: rotate(0deg) translateX(8px) rotate(0deg); }

	100% {
		transform: rotate(-360deg) translateX(8px) rotate(360deg); } }

@keyframes pace-spinner {
	0% {
		transform: rotate(0deg); }

	100% {
		transform: rotate(360deg); } }

.slideInOutRight {
	animation-name: slideInOutRight; }

@keyframes slideInOutRight {
	0% {
		transform: translate3d(-100%, 0, 0); }

	50% {
		transform: translate3d(0%, 0, 0); }

	100% {
		visibility: hidden;
		transform: translate3d(100%, 0, 0); } }

@keyframes slideInL {
	0% {
		transform: translate3d(-100%, 0, 0);
		opacity: 0;
		filter: brightness(100%); }
	30% {
		opacity: 1;
		filter: brightness(150%); }
	80% {
		filter: brightness(300%); }
	100% {
		transform: translate3d(0%, 0, 0);
		filter: brightness(100%); } }

@keyframes slideInR {
	0% {
		transform: translate3d(100%, 0, 0);
		opacity: 0;
		filter: brightness(100%); }
	30% {
		opacity: 1;
		filter: brightness(150%); }
	80% {
		filter: brightness(300%); }
	100% {
		transform: translate3d(0%, 0, 0);
		filter: brightness(100%); } }


@keyframes shiny {
	0% {
		filter: brightness(100%); }
	30% {
		filter: brightness(130%); }
	80% {
		filter: brightness(95%); }
	100% {
		filter: brightness(100%); } }

@keyframes bounceInShiny {
	from,
	20%,
	40%,
	60%,
	80%,
	to {
		animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }


	0% {
		opacity: 0;
		transform: scale3d(0.3, 0.3, 0.3);
		filter: brightness(100%); }


	20% {
		transform: scale3d(1.1, 1.1, 1.1);
		filter: brightness(150%); }


	40% {
		transform: scale3d(0.9, 0.9, 0.9);
		filter: brightness(300%); }


	60% {
		opacity: 1;
		transform: scale3d(1.03, 1.03, 1.03);
		filter: brightness(120%); }



	80% {
		transform: scale3d(0.97, 0.97, 0.97);
		filter: brightness(110%); }


	to {
		opacity: 1;
		transform: scale3d(1, 1, 1);
		filter: brightness(100%); } }


@keyframes bounceBigInShiny {
	from,
	20%,
	40%,
	60%,
	80%,
	to {
		animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }


	0% {
		opacity: 0;
		transform: scale3d(1.7, 1.7, 1.7);
		filter: brightness(100%); }


	20% {
		transform: scale3d(1.1, 1.1, 1.1);
		filter: brightness(150%); }


	40% {
		transform: scale3d(1.02, 1.02, 1.02);
		filter: brightness(200%); }


	60% {
		opacity: 1;
		transform: scale3d(1.06, 1.06, 1.06);
		filter: brightness(300%); }



	80% {
		transform: scale3d(1, 1, 1);
		filter: brightness(150%); }


	to {
		opacity: 1;
		transform: scale3d(1.03, 1.03, 1.03);
		filter: brightness(100%); } }

@keyframes lighting {
	0% {
		opacity: 1; }
	50% {
		opacity: .2; }
	100% {
		opacity: 1; } }

